import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import Item from './Item';
//import styleCSS from "./style.css"


const outerDiv = {
    width: '20vh',
    height: '20vh',
    backgroundColor: '#fff',
    

 
  };

const imageStyle = {
    maxWidth: '80%',
    maxHeight: '80%'
}

const PaperStyle = {
    padding: '20px'
}

const h2Style = {
    display: 'flex',
    maxWidth: '80%',
    maxHeight: '80%',
    textSize: '10px'
}


class Skill_item extends Component {


    render() {
     
        return (
        <div style={outerDiv} class="SkillHover" >
            <Paper style={PaperStyle}>
            <img style={imageStyle} src={require("./images/" + this.props.data.image)} alt=""/>
            <h2 style={h2Style}>{this.props.data.title} </h2>
            </Paper>
        </div>
        )
      
 
    }
}

export default Skill_item;


